.alert {
  width: -webkit-fill-available;
  position: fixed;
  left: 0;
  padding: 1rem;
  font-size: large;
  border-radius: 0.3rem;
  margin: 0.3rem;
  z-index: 1;
}
.alert svg {
  float: right;
}
.alert.error {
  color: #6e1717;
  border: 2px solid #b82424;
  background-color: rgba(255, 203, 203, 0.95);
}
.alert.warning {
  color: #6e6c17;
  border: 2px solid #b8a924;
  background-color: rgba(255, 243, 203, 0.95);
}
.alert.success {
  color: #176e1e;
  border: 2px solid #29b824;
  background-color: rgba(203, 255, 203, 0.95);
}
