.modalContent {
  min-width: 30rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
}
.form {
  padding: 1rem;
  padding-bottom: 3rem;
}
.form input {
  width: 100%;
  box-sizing: border-box;
}
.form .actionButton {
  position: absolute;
  right: 1rem;
  bottom: 1rem;
}
.form .submit {
  position: absolute;
  right: 7rem;
  background-color: #323232;
  color: white;
}
.modalTitle {
  padding: 1rem 1rem 0.5rem 1rem;
  background-color: #323232;
  color: white;
}
.formFooter {
  height: 50px;
}
