:global .enact-body-fit {
  margin: 0;
  height: 100%;
  overflow: auto;
  position: relative;
}
:global .enact-fit {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}
:global .enact-clip {
  overflow: hidden;
}
:global .enact-no-touch-action {
  -ms-touch-action: none;
}
:global .enact-untouchable {
  pointer-events: none;
}
:global .enact-untouchable > * {
  pointer-events: auto;
}
:global .enact-unselectable {
  cursor: default;
  -ms-user-select: none;
  -webkit-user-select: none;
  -moz-user-select: -moz-none;
  user-select: none;
}
:global .enact-selectable {
  cursor: auto;
  -ms-user-select: element;
  -webkit-user-select: text;
  -moz-user-select: text;
  user-select: text;
}
:global .debug.aria [aria-label]::after,
:global .debug.aria [aria-valuetext]::after {
  position: absolute;
  font-size: small;
  font-weight: 100;
  background-color: #ffaa0099;
  color: white;
  border: 1px solid #ffaa00;
  border-radius: 0.5em;
  border-top-left-radius: 0;
  bottom: 0;
  padding: 0 1ex;
  transform: translateY(100%);
  line-height: 1.3em;
  z-index: 2;
  max-width: 100%;
  pointer-events: none;
}
:global .debug.aria [aria-label]::after {
  content: "Aria Label:\0020" attr(aria-label);
}
:global .debug.aria [aria-valuetext]::after {
  content: "Aria Value:\0020" attr(aria-valuetext);
}
:global .debug.aria [aria-label][aria-valuetext]::after {
  content: "Aria Label:\0020'" attr(aria-label) "'; Aria Value:\0020'" attr(aria-valuetext) "';";
}
.app {
  height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: rgba(238, 241, 245, 0.863);
}
:global(.react-datepicker__input-container) {
  width: 290px;
}
main {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: auto;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
}
.inputLabel {
  font-weight: 500;
  margin: 0.3rem 0;
}
.inputDescription {
  color: #7a7a7a;
  font-size: small;
  margin: 0.3rem 0;
}
.pageContainer {
  height: 100%;
}
.formWarning {
  font-size: x-small;
  color: #a20101;
}
.cardItemContainer {
  padding: 2rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.cardItem {
  height: fit-content;
  background-color: white;
  position: relative;
  padding: 10px 20px;
  border: 1px solid rgba(231, 234, 238, 0.863);
  border-radius: 10px;
  width: calc(50% - 4rem);
  word-wrap: break-word;
  margin-bottom: 2rem;
}
.cardItem:hover {
  box-shadow: 0 0 0.3rem 0.3rem rgba(222, 227, 233, 0.904);
}
.cardItem svg:hover {
  color: #1f50f3;
}
.cardName {
  font-size: x-large;
  margin-right: 3.5rem;
}
.cardUrl {
  margin-top: 0.5rem;
  font-size: small;
  color: #838383;
}
.cardDelete,
.cardEdit,
.cardSetting {
  position: absolute;
  top: 0.8rem;
  right: 0.5rem;
}
.cardEdit {
  right: 2rem;
}
.cardSetting {
  right: 3.5rem;
}
.cardBottom * {
  margin-right: 0.5rem;
}
body {
  padding: 0;
  margin: 0;
  font-family: "Haas Grot Text R Web", "Helvetica Neue", Helvetica, Arial, sans-serif;
  color: #323232;
  position: relative;
  height: 100%;
  width: 100%;
}
button,
input,
select,
textarea {
  font-size: 1rem;
  color: #323232;
  background-color: transparent;
  border: 1px solid #797979;
  height: 2.5rem;
  display: inline-block;
  padding: 10px 15px 11px;
  font-weight: 500;
  line-height: 17px;
  letter-spacing: 0.02em;
  vertical-align: middle;
  box-shadow: none;
  text-decoration: none;
  border-radius: 4px;
}
button,
select {
  cursor: pointer;
}
.numberInput {
  width: 20px;
}
textarea {
  width: calc(100% - 2rem);
  min-height: 350px;
  max-height: 300px;
  overflow-y: scroll;
  resize: none;
}
.primaryButton {
  color: white;
  background-color: #1f50f3;
  border: none;
}
.secondaryButton {
  border: 1px solid #1f50f3;
  color: #1f50f3;
  background-color: white;
}
input:disabled,
select:disabled {
  cursor: not-allowed;
  opacity: 0.6;
}
table {
  min-width: 80%;
  margin: auto;
  table-layout: fixed;
  border-collapse: collapse;
  border: 1px solid rgba(139, 139, 219, 0.3);
  border-spacing: 0;
}
table tr:not(:last-child) {
  border-bottom: 2px solid rgba(139, 139, 219, 0.3);
}
table td {
  padding: 10px 20px;
}
table td:nth-last-child(-n+3) {
  width: 15px;
}
@keyframes scaleIn {
  from {
    transform: scale(0.5, 0.5);
    opacity: 0.5;
  }
  to {
    transform: scale(2.5, 2.5);
    opacity: 0;
  }
}
.statusDot {
  height: 0.75rem;
  width: 0.75rem;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
}
.statusDot.invalid {
  background-color: rgba(223, 43, 43, 0.8);
}
.statusDot.valid {
  background-color: rgba(35, 214, 116, 0.8);
}
.statusDot.downloading {
  background-color: rgba(214, 151, 35, 0.8);
  animation: scaleIn 3s infinite cubic-bezier(0.36, 0.11, 0.89, 0.32);
}
