.simulationManager {
  height: 100%;
}
.tabButton {
  border: none;
  color: gray;
}
.tabButton:focus {
  outline: none;
}
.tabButton:hover {
  color: rgba(0, 0, 0, 0.685);
}
.tabButton.selected {
  color: #323232;
  border-bottom: 2px solid #416cf8;
  border-radius: unset;
}
.connectionField {
  display: flex;
  justify-content: space-between;
}
.formIcons {
  font-size: 2.35rem;
}
.weatherInput {
  width: 48%;
  display: inline-block;
  padding: 5px;
}
.rightFloatContainer {
  font-size: xx-small;
}
.react-datepicker__input-container {
  width: 290px;
}
